/**
 * Game loop class
 *
 * Written largely from: https://isaacsukin.com/news/2015/01/detailed-explanation-javascript-game-loops-and-timing
 */
class Loop {
    constructor(update, draw) {
        this.looping = false;
        this.delta = 0;
        this.lastFrameTimeMs = 0;
        this.timestep = 1000 / 60; // timeSimulatedPerFrame
        this.fps = 60;
        this.framesThisSecond = 0;
        this.lastFpsUpdate = 0;
        this.fpsDecayWeight = 0.25;
        this.fpsDisplay = document.getElementById("fps-display");
        this.setInnerFunctions = (update, draw) => {
            this.setUpdate(update);
            this.setDraw(draw);
        };
        this.setUpdate = (update) => {
            this.update = update;
        };
        this.setDraw = (draw) => {
            this.draw = draw;
        };
        this.run = (timestamp) => {
            // Track Fps
            if (this.looping) {
                if (timestamp > this.lastFpsUpdate + 1000) {
                    this.fps = this.fpsDecayWeight * this.framesThisSecond + (1 - this.fpsDecayWeight) * this.fps;
                    this.lastFpsUpdate = timestamp;
                    this.framesThisSecond = 0;
                }
                this.framesThisSecond++;
                // Track accumulated time that hasn't been spent yet
                this.delta += timestamp - this.lastFrameTimeMs;
                this.lastFrameTimeMs = timestamp;
                // Update word for every timestep that comprises delta time
                let numberOfSequentialUpdates = 0;
                while (this.delta >= this.timestep) {
                    this.update(this.timestep);
                    this.delta -= this.timestep;
                    if (++numberOfSequentialUpdates >= 240) { // detect spiral of death
                        this.panic(); // fix things
                        break; // break out
                    }
                }
                this.draw();
                if (this.fpsDisplay) {
                    this.fpsDisplay.textContent = Math.round(this.fps) + ' FPS';
                }
                window.requestAnimationFrame(this.run);
            }
        };
        this.panic = () => {
            console.log("<PANIC>");
            //this.stop()
        };
        this.start = (timestamp) => {
            this.looping = true;
            this.run(timestamp);
        };
        this.stop = () => {
            this.looping = false;
        };
        this.update = update;
        this.draw = draw;
    }
}
exports.default = Loop;
